.section {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#navBarHome {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: auto;
  display: none;
}

#homeLogo {
  width: 40%;
  height: 20vh;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.htmlCanvas {
  width: 100vw;
  height: 100vh;
}

#canvasContainer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100dvh;
  z-index: 1;
}

.highLightRectangleNav {
  position: absolute;
  top: 50%;
  height: 125px;
  width: 250px;
  border: 2px solid red;
  transition: 1s;
  z-index: 2;
  transform: translateY(-50%);
}

#highLightRectangleAbout {
  left: 10%;
}

#highLightRectangleGallery {
  right: 10%;
}

#loading {
  transform: translate(-50%, -51.8%);
  width: 100vw;
  height: 100vh;
  background-color: black;
}

.talentScroll {
  width: auto;
  overflow-x: scroll;
  height: 30vh;
  display: flex;
  justify-content: center;
}

.talent {
  width: 20vw;
  height: 100%;
  background-color: red;
}